.form-container {
	color: #ffffff;
	display: flex;
	justify-content: center;
	margin: 80px 0px;
}

.form-container > form > .form-title {
	font-weight: 400;
}

.form-container > form {
	border-radius: 20px;
	margin: 36px 64px;
}

/* Media query */
@media screen and (max-width: 768px) {
	.form-container {
		margin: 12px 0px;
	}
	.form-container > form {
		margin: 0px 24px;
	}
	.form-container > form > .form-title {
		font-size: 1.125rem;
	}
}

@media screen and (max-width: 480px) {
	.form-container > form > .form-title {
		font-size: 1rem;
	}
}
